.form {
  padding: 25px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  input {
    margin: 0 auto;
    border: 1px solid green;
  }
}

.form__group {
  display: flex;
  margin: 10px;
  label {
    display: flex;
    min-width: 10rem;
    margin: 10px;
  }
  input {
    flex: 1;
  }
}
